___CSS_LOADER_EXPORT___.push([module.id, `.info__img-block {
  display: flex;
  justify-content: space-between;
  margin-top: 44px;
  position: relative;
}

.info__img {
  max-width: 120px;
  max-height: 120px;
  object-fit: contain;
}

.info__img_title {
  font-size: 16px;
  font-weight: 400;
}

.info__img_descr {
  color: var(--6, #999);
  font-size: 12px;
  margin-top: 16px;
  font-weight: 400;
}

.info__input-file {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

.info__img_texts {
  margin-top: 16px;
  margin-left: 24px;
}

.dot-pulse {
  position: relative;
  left: -9999px;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  background-color: #ad7955;
  color: #ad7955;
  box-shadow: 9999px 0 0 -5px;
  animation: dot-pulse 1.5s infinite linear;
  animation-delay: 0.25s;
}

.dot-pulse::before,
.dot-pulse::after {
  content: "";
  display: inline-block;
  position: absolute;
  top: 0;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  background-color: #ad7955;
  color: #ad7955;
}

.dot-pulse::before {
  box-shadow: 9984px 0 0 -5px;
  animation: dot-pulse-before 1.5s infinite linear;
  animation-delay: 0s;
}

.dot-pulse::after {
  box-shadow: 10014px 0 0 -5px;
  animation: dot-pulse-after 1.5s infinite linear;
  animation-delay: 0.5s;
}

@keyframes dot-pulse-before {
  0% {
    box-shadow: 9984px 0 0 -5px;
  }
  30% {
    box-shadow: 9984px 0 0 2px;
  }
  60%, 100% {
    box-shadow: 9984px 0 0 -5px;
  }
}
@keyframes dot-pulse {
  0% {
    box-shadow: 9999px 0 0 -5px;
  }
  30% {
    box-shadow: 9999px 0 0 2px;
  }
  60%, 100% {
    box-shadow: 9999px 0 0 -5px;
  }
}
@keyframes dot-pulse-after {
  0% {
    box-shadow: 10014px 0 0 -5px;
  }
  30% {
    box-shadow: 10014px 0 0 2px;
  }
  60%, 100% {
    box-shadow: 10014px 0 0 -5px;
  }
}
@media screen and (max-width: 576px) {
  .info__img-block {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .info__img_texts {
    margin-left: 0;
    margin-top: 30px;
    text-align: center;
  }
